import { CgMathPlus } from "react-icons/cg";
import { useEffect, useState } from "react";
import Post from "./Post";
import * as postList from "./styles/blog-posts.module.css";

const PostsList = (props) => {
  const [posts, setPosts] = useState();

  const getPosts = () => {
    fetch("https://jsonplaceholder.typicode.com/posts")
      .then((response) => response.json())
      .then((json) => setPosts(json));
  };

  useEffect(() => {
    getPosts();
  }, []);

  return (
    <section className={"blog-posts"}>
      <section className={postList["blog-posts-header"]}>
        <h2>Blog Posts</h2>
        <a
          href={"new-post"}
          className={postList["create-new-post-icon"]}
          onClick={() => {
            props.setCurrentView("create-new-post");
          }}
        >
          <CgMathPlus />
        </a>
      </section>
      {posts && (
        <ul className={postList["posts-list"]}>
          {posts.map((post, i) => (
            <Post
              getPosts={getPosts}
              index={i}
              title={post.title}
              id={post.id}
              body={post.body}
            />
          ))}
        </ul>
      )}
    </section>
  );
};

export default PostsList;
